import * as actionTypes from "../actions/actionTypes";
import { updateObejct } from "../utility";

const initialState = {
  error: null,
  loading: false,
  profile: {},
  myProjectsLoading: false,
  myProjects: [],
  myReferrals: [],
};

const profileStart = (state) => {
  return updateObejct(state, {
    error: null,
    loading: true,
    profile: {},
  });
};

const profileFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    loading: false,
  });
};

const profileSuccess = (state, action) => {
  return updateObejct(state, {
    loading: false,
    error: null,
    profile: action.profile,
  });
};

const editProfile = (state, action) => {
  return updateObejct(state, {
    profile: action.profile,
  });
};

const myProjectsStart = (state, action) => {
  return updateObejct(state, {
    error: null,
    myProjectsLoading: true,
  });
};

const myProjectsFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    myProjectsLoading: false,
  });
};

const myProjectsSuccess = (state, action) => {
  return updateObejct(state, {
    error: null,
    myProjectsLoading: false,
    myProjects: action.projects,
  });
};

const myReferralsFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
  });
};

const myReferralsSuccess = (state, action) => {
  return updateObejct(state, {
    error: null,
    myReferrals: action.refferals,
  });
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.PROFILE_START:
      return profileStart(state, action);
    case actionTypes.PROFILE_FAIL:
      return profileFail(state, action);
    case actionTypes.PROFILE_SUCCESS:
      return profileSuccess(state, action);
    case actionTypes.EDIT_PROFILE:
      return editProfile(state, action);
    case actionTypes.MYPROJECTS_START:
      return myProjectsStart(state, action);
    case actionTypes.MYPROJECTS_FAIL:
      return myProjectsFail(state, action);
    case actionTypes.MYPROJECTS_SUCCESS:
      return myProjectsSuccess(state, action);
    case actionTypes.REFFERALS_FAIL:
      return myReferralsFail(state, action);
    case actionTypes.REFERRALS_SUCCESSS:
      return myReferralsSuccess(state, action);
    default:
      return state;
  }
};

export default profileReducer;
