import React, { Component, lazy } from "react";
import { Switch, Route, withRouter, Redirect } from "react-router-dom";
// import {
//   fetchAllAssignments
// } from "../redux/actions/instructor";
import {
  fetchAllCourses,
  fetchMyCourses,
  fetchCourseData,
  fetchModuleData,
  fetchQuizData,
  submitQuiz,
  contentDone,
  buyNow,
  fetchSlots,
} from "../redux/actions/course";
import {
  authCheck,
  authLogin,
  authSignup,
  verifyEmail,
  authLogout,
  googleLogin,
  // facebookLogin,
  fetchcsrfToken,
  // clearMsg,
  forgotPass,
  resetPass,
} from "../redux/actions/auth";
import {
  fetchProfile,
  editProfile,
  fetchMyProjects,
  addProject,
  fetchMyReferrals,
} from "../redux/actions/profile";
import {
  fetchThreads,
  fetchThreadDetail,
  createThread,
  followUp,
  // fetchSlots,
  fetchMySlots,
} from "../redux/actions/forum";
import { clearMsg, setMsg } from "../redux/actions/msg";
import { Coursesobj } from "./Coursesobj";
import { connect } from "react-redux";
import Login from "./NewLogin/Login";
import SignUp from "./NewSignup/Signup";
import Contact from "./Contact";
// import CourseOverview from "./CourseVideo/CourseVideo";
// import Course from "./CourseVideo/CourseVideo";
const HomeComponent = lazy(() => import("./Home/HomeComponent"));
const Data = lazy(() => import("./Data/Data"));
// const CourseOverview = lazy(() => import("./CourseOverview/CourseOverview"));
const About = lazy(() => import("./About/About"));
const VerifyMail = lazy(() => import("./verifyMail"));
const Header = lazy(() => import("./Header/Header"));
const ProtectedRoute = lazy(() => import("./Protected"));
const Profile = lazy(() => import("./Profile/Profile"));
const CourseDetail = lazy(() => import("./CourseDetail/CourseDetail"));
const WinnersComponent = lazy(() => import("./Winners/WinnersComponent"));
const Forum = lazy(() => import("./Forum/Forum"));
const ThreadList = lazy(() => import("./ThreadList"));
const Thread = lazy(() => import("./Thread"));
const AssignmentEvaluate = lazy(() =>
  import("./AssignmentsEvaluate/AssignmentsEvaluate")
);
const PurchasesList = lazy(() => import("./Purchases/PurchasesList"));
// const StudentDashboard = lazy(() => import("./StudentDashboard/StudentDashboard"));
const ApplyRefferal = lazy(() => import("./applyRefferal/ApplyRefferal"));
const Learn = lazy(() => import("./Learn/Learn"));
const ResetPass = lazy(() => import("./ResetPass/ResetPassword"));
const Refer = lazy(() => import("./Refer/Refer"));
const Course = lazy(() => import("./CourseVideo/CourseVideo"));

const mapStateToProps = (state) => {
  return {
    auth: state.auth,
    courses: state.courses,
    profile: state.profile,
    forum: state.forum,
    msg: state.msg,
  };
};

const mapDispatchToProps = (dispatch) => ({
  authCheck: () => dispatch(authCheck()),
  authLogin: (username, password, pathname, onSuccessLogin, onFailedLogin) =>
    dispatch(
      authLogin(username, password, pathname, onSuccessLogin, onFailedLogin)
    ),
  googleLogin: (accessToken, onSuccessLogin, onFailedLogin, pathname) =>
    dispatch(googleLogin(accessToken, onSuccessLogin, onFailedLogin, pathname)),
  // facebookLogin: (accessToken, onSuccessLogin, onFailedLogin) =>
  //   dispatch(facebookLogin(accessToken, onSuccessLogin, onFailedLogin)),
  authSignup: (
    username,
    email,
    password,
    first_name,
    last_name,
    code,
    onSuccessSignup,
    onFailSignup
  ) =>
    dispatch(
      authSignup(
        username,
        email,
        password,
        first_name,
        last_name,
        code,
        onSuccessSignup,
        onFailSignup
      )
    ),
  verifyEmail: (key, succesVerify) => dispatch(verifyEmail(key, succesVerify)),
  authLogout: (onSuccessLogout) => dispatch(authLogout(onSuccessLogout)),
  fetchAllCourses: () => dispatch(fetchAllCourses()),
  fetchcsrfToken: () => dispatch(fetchcsrfToken()),
  fetchProfile: () => dispatch(fetchProfile()),
  editProfile: (
    first_name,
    last_name,
    date_of_birth,
    organization,
    location,
    state,
    country,
    profile,
    gender,
    std,
    contact_number,
    successEdit,
    failedEdit
  ) =>
    dispatch(
      editProfile(
        first_name,
        last_name,
        date_of_birth,
        organization,
        location,
        state,
        country,
        profile,
        gender,
        std,
        contact_number,
        successEdit,
        failedEdit
      )
    ),
  fetchMyCourses: () => dispatch(fetchMyCourses()),
  fetchCourseData: (course_slug, onSuccessData, onFailedData) =>
    dispatch(fetchCourseData(course_slug, onSuccessData, onFailedData)),
  fetchModuleData: (course_slug, module_slug, onSuccessData, onFailedData) =>
    dispatch(
      fetchModuleData(course_slug, module_slug, onSuccessData, onFailedData)
    ),
  fetchQuizData: (source, onSuccessQuiz) =>
    dispatch(fetchQuizData(source, onSuccessQuiz)),
  submitQuiz: (quizId, score) => dispatch(submitQuiz(quizId, score)),
  fetchMyProjects: () => dispatch(fetchMyProjects()),
  addProject: (title, description, link, image, success, failed) =>
    dispatch(addProject(title, description, link, image, success, failed)),
  clearMsg: () => dispatch(clearMsg()),
  setMsg: (msg) => dispatch(setMsg(msg)),
  forgotPass: (email, onSuccessForgot, onFailForgot) =>
    dispatch(forgotPass(email, onSuccessForgot, onFailForgot)),
  resetPass: (password, user_id, token, onSuccessReset) =>
    dispatch(resetPass(password, user_id, token, onSuccessReset)),
  contentDone: (pk, module_slug, content_slug, success, failed) =>
    dispatch(contentDone(pk, module_slug, content_slug, success, failed)),
  fetchThreads: (Module, success, failed) =>
    dispatch(fetchThreads(Module, success, failed)),
  fetchThreadDetail: (thread_id) => dispatch(fetchThreadDetail(thread_id)),
  createThread: (
    module_slug,
    title,
    description,
    images,
    additional_info,
    slot,
    successCreate,
    slotBooked
  ) =>
    dispatch(
      createThread(
        module_slug,
        title,
        description,
        images,
        additional_info,
        slot,
        successCreate,
        slotBooked
      )
    ),
  followUp: (thread_id, description, images) =>
    dispatch(followUp(thread_id, description, images)),
  fetchSlots: (course_slug, success) =>
    dispatch(fetchSlots(course_slug, success)),
  buyNow: (stripePromise) => dispatch(buyNow(stripePromise)),
  fetchMyReferrals: () => dispatch(fetchMyReferrals()),
  fetchMySlots: () => dispatch(fetchMySlots()),
});

class MainComponent extends Component {
  constructor(props) {
    super(props);
    this.previousLocation = this.props.location;
    this.state = {
      coursesobj: Coursesobj,
    };
  }

  componentDidMount() {
    this.props.fetchcsrfToken();
    this.props.authCheck();
    this.props.fetchAllCourses();
    if (localStorage.getItem("token")) {
      this.props.fetchProfile();
      this.props.fetchMyCourses();
      this.props.fetchMyProjects();
      this.props.fetchMyReferrals();
    }
  }

  componentDidUpdate(prevProps) {
    let { location } = this.props;

    if (!(location.state && location.state.modal)) {
      this.previousLocation = location;
    }

    // Scroll to top after redirecting
    if (this.props.location.pathname !== prevProps.location.pathname) {
      window.scrollTo(0, 0);
    }
  }

  render() {
    const { location } = this.props;
    const isModal =
      location.state &&
      location.state.modal &&
      this.previousLocation !== location;

    const redirected =
      location.state && location.state.redirected ? true : false;

    const CourseDetailwithId = ({ match }) => {
      return (
        <CourseDetail
          course={
            this.state.coursesobj.filter(
              (course) => course.id === parseInt(match.params.id, 10)
            )[0]
          }
          auth={this.props.auth}
          myCourses={this.props.courses.myCourses}
          buyNow={this.props.buyNow}
        />
      );
    };
    const Head = withRouter(Header);
    return (
      <React.Fragment>
        <Head
          auth={this.props.auth}
          authLogout={this.props.authLogout}
          // fetchProfile={this.props.fetchProfile}
          profile={this.props.profile.profile}
          clearMsg={this.props.clearMsg}
          msg={this.props.msg}
          fetchProfile={this.props.fetchProfile}
        />
        <Switch location={isModal ? this.previousLocation : location}>
          <Route
            exact
            path="/home"
            component={() => <HomeComponent auth={this.props.auth} />}
          />
          <Route exact path="/competition" component={WinnersComponent} />
          {/* <Route exact path="/courses" component={() => <CoursePage />} /> */}
          <Route exact path="/referral/:referCode">
            <ApplyRefferal authSignup={this.props.authSignup} />
          </Route>
          {/* <Route exact path="/learn/:id/:a" component={() => <CourseOverview fetchCourseData={this.props.courses} />} /> */}
          <Route path="/courses/:id" component={CourseDetailwithId} />
          <Route exact path="/about" component={() => <About />} />
          <Route
            exact
            path="/purchaseslist"
            component={() => <PurchasesList />}
          />
          {/* <Route exact path="/overview" component={() => <CourseOverview />} /> */}
          <Route exact path="/contact" component={() => <Contact />} />
          {/* // <<<<<<< develop */}
          {/* <Route exact path="/course/:course" component={CourseVideo} />
          <Route
            exact
            path="/course/:course/:module/:title"
            component={() => (
              <CourseVideo
                courses={this.state.coursesobj}
                fetchModule={this.props.fetchThreads}
              />
            )}
          />
          

======= */}
          <Route exact path="/course/:course/:module/:tabOpen/:title">
            <Course
              courses={this.props.courses}
              fetchCourseData={this.props.fetchCourseData}
              fetchModuleData={this.props.fetchModuleData}
              contentDone={this.props.contentDone}
              profile={this.props.profile}
              submitQuiz={this.props.submitQuiz}
            />
          </Route>
          <Route exact path="/course/:course/:module/:tabOpen/:type/:quiz">
            <Course
              courses={this.props.courses}
              fetchCourseData={this.props.fetchCourseData}
              fetchModuleData={this.props.fetchModuleData}
              profile={this.props.profile}
              submitQuiz={this.props.submitQuiz}
            />
          </Route>
          {/* <Route exact path="/course/:course">
            <Course
              courses={this.props.courses}
              fetchCourseData={this.props.fetchCourseData}
              fetchModuleData={this.props.fetchModuleData}
              createThread={this.props.createThread}
              followUp={this.props.followUp}
            />
          </Route>
          <Route exact path="/course/:course/:module">
            <Course
              courses={this.props.courses}
              fetchCourseData={this.props.fetchCourseData}
              fetchModuleData={this.props.fetchModuleData}
              contentDone={this.props.contentDone}
              createThread={this.props.createThread}
              followUp={this.props.followUp}
            />
          </Route> */}
          {/* Dynamic Route for tabs */}

          <Route exact path="/course/:course/:module/:tabOpen">
            <Course
              courses={this.props.courses}
              fetchCourseData={this.props.fetchCourseData}
              fetchModuleData={this.props.fetchModuleData}
              contentDone={this.props.contentDone}
              createThread={this.props.createThread}
              followUp={this.props.followUp}
              fetchSlots={this.props.fetchSlots}
              profile={this.props.profile}
              submitQuiz={this.props.submitQuiz}
            />
          </Route>
          {/* Tab routes close */}
          {/* // >>>>>>> develop */}
          <Route exact path="/login">
            <Login
              auth={this.props.auth}
              isModal={isModal}
              prevLocation={this.previousLocation}
              authLogin={this.props.authLogin}
              forgotPass={this.props.forgotPass}
              googleLogin={this.props.googleLogin}
              redirected={redirected}
              clearMsg={this.props.clearMsg}
              msg={this.props.msg}
              // facebookLogin={this.props.facebookLogin}
            />
          </Route>
          <Route exact path="/signup">
            <SignUp
              auth={this.props.auth}
              isModal={isModal}
              prevLocation={this.previousLocation}
              authSignup={this.props.authSignup}
              googleLogin={this.props.googleLogin}
              redirected={redirected}
              clearMsg={this.props.clearMsg}
              msg={this.props.msg}
              // facebookLogin={this.props.facebookLogin}
            />
          </Route>
          <Route
            exact
            path="/verify-user/:key"
            component={() => (
              <VerifyMail
                auth={this.props.auth}
                verifyEmail={this.props.verifyEmail}
              />
            )}
          />
          <Route
            exact
            path="/reset-password/:key"
            component={() => (
              <ResetPass
                auth={this.props.auth}
                resetPass={this.props.resetPass}
              />
            )}
          />
          <Route
            exact
            path="/data"
            component={() => (
              <Data
                editProfile={this.props.editProfile}
                auth={this.props.auth}
                resetPass={this.props.resetPass}
              />
            )}
          />
          <ProtectedRoute
            exact
            path="/refer"
            profile={this.props.profile.profile}
            myReferrals={this.props.profile.myReferrals}
            setMsg={this.props.setMsg}
            component={Refer}
          />
          <ProtectedRoute
            exact={true}
            path="/profile"
            profile={this.props.profile}
            editProfile={this.props.editProfile}
            fetchProfile={this.props.fetchProfile}
            fetchMyCourses={this.props.fetchMyCourses}
            fetchMyProjects={this.props.fetchMyProjects}
            addProject={this.props.addProject}
            courses={this.props.courses}
            component={Profile}
            auth={this.props.auth}
          />
          {/* <ProtectedRoute
            exact={true}
            path="/learn/:course"
            component={CourseOverview}
            auth={this.props.auth}
            fetchCourseData={this.props.fetchCourseData}
            fetchMySlots={this.props.fetchMySlots}
            courses={this.props.courses}
            mySlots={this.props.forum.mySlots}
          /> */}
          <ProtectedRoute
            exact={true}
            path="/learn/:course/:module/:content"
            component={Learn}
            auth={this.props.auth}
            fetchCourseData={this.props.fetchCourseData}
            fetchQuizData={this.props.fetchQuizData}
            submitQuiz={this.props.submitQuiz}
            courses={this.props.courses}
            contentDone={this.props.contentDone}
          />
          <ProtectedRoute
            exact
            path="/assignmentevaluate"
            component={AssignmentEvaluate}
            allCourses={this.props.courses.allCourses}
          />
          <ProtectedRoute
            exact
            path="/forum/:course"
            component={Forum}
            courses={this.props.courses}
            fetchCourseData={this.props.fetchCourseData}
          />
          <ProtectedRoute
            exact
            path="/forum/:course/:forum_id/:module/:module_id"
            component={ThreadList}
            fetchThreads={this.props.fetchThreads}
            createThread={this.props.createThread}
            forum={this.props.forum}
            courses={this.props.courses}
            fetchSlots={this.props.fetchSlots}
          />
          <ProtectedRoute
            exact
            path="/forum/:course/:module/:thread_id"
            component={Thread}
            fetchThreadDetail={this.props.fetchThreadDetail}
            followUp={this.props.followUp}
            forum={this.props.forum}
          />
          <Redirect to="/home" />
        </Switch>
      </React.Fragment>
    );
  }
}
export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(MainComponent)
);
