import * as actionTypes from "./actionTypes";
import { fetchProfile, fetchMyReferrals, fetchMyProjects } from "./profile";
import { fetchMyCourses } from "./course";
import { config } from "../../constants";
import { clearMsg } from "./msg";

export const displayError = (error) => ({
  type: actionTypes.FAIL_MSG,
  error: error,
});

export const displaySuccess = (msg) => ({
  type: actionTypes.SUCCESS_MSG,
  msg: msg,
});

export const authStart = () => ({
  type: actionTypes.AUTH_START,
});

export const authSuccess = (token) => ({
  type: actionTypes.AUTH_SUCCESS,
  token: token,
});

export const signupSuccess = () => ({
  type: actionTypes.SUCCESS_MSG,
  msg: "Email verification link has been sent to your email",
});

export const authFail = (err) => ({
  type: actionTypes.AUTH_FAIL,
  error: err,
  token: null,
});

export const signupFail = (error) => ({
  type: actionTypes.SIGNUP_FAIL,
  error: error,
});

export const logout = () => {
  localStorage.removeItem("token");
  localStorage.removeItem("csrf");
  return {
    type: actionTypes.AUTH_LOGOUT,
  };
};

