export const authLogin = (
  username,
  password,
  pathname,
  onSuccessLogin,
  onFailedLogin
) => (dispatch) => {
  dispatch(authStart());
  // let cookiearray = document.cookie.split(";");
  // let csrf = "";
  // for (var i = 0; i < cookiearray.length; i++) {
  //   let name = cookiearray[i].split("=")[0];
  //   let value = cookiearray[i].split("=")[1];
  //   if (name === " csrftoken") {
  //     csrf = value;
  //   }
  // }

  const validEmail = (val) =>
    /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);

  let csrf = localStorage.getItem("csrf");

  let body = {};

  if (validEmail(username)) {
    body.email = username;
  } else {
    body.username = username;
  }

  body.password = password;

  let bodyFinal = JSON.stringify(body);

  return fetch(config.url.API_URL + "/users/login", {
    method: "POST",
    body: bodyFinal,
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      "X-CSRFToken": csrf,
    },
  })
    .then(
      (res) => {
        if (res.ok) {
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        throw error;
      }
    )
    .then((res) => res.json())
    .then((res) => {
      if (res.key !== undefined) {
        const token = res.key;
        localStorage.setItem("token", token);
        dispatch(authSuccess(token));
        let cookiearray = document.cookie.split(";");
        let csrf = "";
        for (var i = 0; i < cookiearray.length; i++) {
          let name = cookiearray[i].split("=")[0];
          let value = cookiearray[i].split("=")[1];
          if (name === " csrftoken") {
            csrf = value;
          }
        }
        localStorage.setItem("csrf", csrf);
        dispatch(fetchProfile());
        dispatch(fetchMyCourses());
        dispatch(fetchMyProjects());
        dispatch(fetchMyReferrals());
        onSuccessLogin(pathname);
      } else {
        var error = new Error("Error " + res.status);
        error.res = res;
        throw error;
      }
    })
    .catch((err) => {
      if (err.res) {
        err.res.json().then((res) => {
          dispatch(authFail(res.non_field_errors[0]));
          dispatch(displayError(res.non_field_errors[0]));
          setTimeout(() => {
            dispatch(clearMsg());
          }, 7000);
          onFailedLogin();
        });
      }
    });
};

export const authLogout = (onSuccessLogout) => (dispatch) => {
  const token = localStorage.getItem("token");
  // let cookiearray = document.cookie.split(";");
  // let csrf = "";
  // for (var i = 0; i < cookiearray.length; i++) {
  //   let name = cookiearray[i].split("=")[0];
  //   let value = cookiearray[i].split("=")[1];
  //   if (name === " csrftoken") {
  //     console.log(value);
  //     csrf = value;
  //   }
  // }
  let csrf = "";
  if (token) {
    csrf = localStorage.getItem("csrf");
  }

  // let csrf = localStorage.getItem('csrf');
  return fetch(config.url.API_URL + "/users/logout", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: "token " + token,
      "X-CSRFToken": csrf,
    },
    body: JSON.stringify({
      revoke_token: true,
    }),
  })
    .then(
      (res) => {
        if (res.ok) {
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        throw error;
      }
    )
    .then(() => {
      dispatch(logout());
      onSuccessLogout();
    })
    .catch((err) => {
      if (err.res) {
        err.res.json().then((res) => {
          dispatch(logout());
          onSuccessLogout();
        });
      }
    });
};

export const fetchcsrfToken = () => () => {
  return fetch(config.url.API_URL + "/get-csrf", {
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },
  })
    .then(
      (res) => {
        if (res.ok) {
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        throw error;
      }
    )
    .then(() => {
      let cookiearray = document.cookie.split(";");
      let csrf = "";
      for (var i = 0; i < cookiearray.length; i++) {
        let name = cookiearray[i].split("=")[0];
        let value = cookiearray[i].split("=")[1];
        if (name === " csrftoken") {
          csrf = value;
        }
      }
      localStorage.setItem("csrf", csrf);
    })
    .catch((err) => {});
};

export const authSignup = (
  username,
  email,
  password,
  first_name,
  last_name,
  code,
  onSuccessSignup,
  onFailSignup
) => (dispatch) => {
  dispatch(authStart());
  // let cookiearray = document.cookie.split(";");
  // let csrf = "";
  // for (var i = 0; i < cookiearray.length; i++) {
  //   let name = cookiearray[i].split("=")[0];
  //   let value = cookiearray[i].split("=")[1];
  //   if (name === " csrftoken") {
  //     csrf = value;
  //   }
  // }
  let csrf = localStorage.getItem("csrf");
  let body = {
    username: username,
    email: email,
    password1: password,
    password2: password,
    first_name: first_name ? first_name : "",
    last_name: last_name ? last_name : "",
  };

  if (code !== null) {
    body.referrer = code;
  }
  return fetch(config.url.API_URL + "/users/register", {
    method: "POST",
    body: JSON.stringify(body),
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      "X-CSRFToken": csrf,
    },
  })
    .then(
      (res) => {
        if (res.ok) {
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        throw error;
      }
    )
    .then((res) => res.json())
    .then((res) => {
      if (res.key !== undefined) {
        const token = res.key;
        localStorage.setItem("token", token);
        dispatch(authSuccess(token));
        dispatch(fetchcsrfToken());
        dispatch(displaySuccess(res.detail));
        setTimeout(() => {
          dispatch(clearMsg());
        }, 7000);
        dispatch(fetchProfile());
        dispatch(fetchMyCourses());
        dispatch(fetchMyProjects());
        dispatch(fetchMyReferrals());
        onSuccessSignup();
      }
      dispatch(signupSuccess());
      setTimeout(() => {
        dispatch(clearMsg());
      }, 7000);
    })
    .catch((err) => {
      if (err.res) {
        err.res.json().then((res) => {
          let msg = "";
          let msg1 = res.email ? "email: " + res.email[0] : "";
          let msg2 = res.username ? res.username[0] : "";
          if (msg1 && msg2) {
            msg = msg1 + " and " + msg2;
          } else {
            msg = msg1 + msg2;
          }
          dispatch(signupFail(msg));
          dispatch(displayError(msg));
          setTimeout(() => {
            dispatch(clearMsg());
          }, 7000);
        });
      }
      onFailSignup();
    });
};

export const authCheck = () => (dispatch) => {
  const token = localStorage.getItem("token");
  if (token === undefined || token === null) {
    dispatch(logout());
  } else {
    dispatch(authSuccess(token));
  }
};

export const verifyEmail = (key, succesVerify) => (dispatch) => {
  dispatch(authStart());
  // let cookiearray = document.cookie.split(";");
  // let csrf = "";
  // for (var i = 0; i < cookiearray.length; i++) {
  //   let name = cookiearray[i].split("=")[0];
  //   let value = cookiearray[i].split("=")[1];
  //   if (name === " csrftoken") {
  //     csrf = value;
  //   }
  // }

  let csrf = localStorage.getItem("csrf");

  const creds = JSON.stringify(key);
  return fetch(config.url.API_URL + "/users/verify-email", {
    method: "POST",
    body: creds,
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      "X-CSRFToken": csrf,
    },
  })
    .then(
      (res) => {
        if (res.ok) {
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        throw error;
      }
    )
    .then((res) => res.json())
    .then((res) => {
      dispatch(displaySuccess("Email verified successfully"));
      setTimeout(() => {
        dispatch(clearMsg());
      }, 7000);
      succesVerify();
    })
    .catch((err) => {
      if (err.res) {
        err.res.json().then((err) => {
          // dispatch(authFail(err.detail));
          dispatch(displayError(err.detail));
          succesVerify();
        });
      }
    });
};

export const googleLogin = (
  access_token,
  onSuccessLogin,
  onFailedLogin,
  pathname
) => (dispatch) => {
  // let cookiearray = document.cookie.split(";");
  // let csrf = "";
  // for (var i = 0; i < cookiearray.length; i++) {
  //   let name = cookiearray[i].split("=")[0];
  //   let value = cookiearray[i].split("=")[1];
  //   if (name === " csrftoken") {
  //     csrf = value;
  //   }
  // }
  dispatch(authStart());
  let csrf = localStorage.getItem("csrf");
  const creds = JSON.stringify({
    access_token: access_token,
  });
  return fetch(config.url.API_URL + "/users/login/google", {
    method: "POST",
    body: creds,
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      "X-CSRFToken": csrf,
    },
  })
    .then(
      (res) => {
        if (res.ok) {
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        throw error;
      }
    )
    .then((res) => res.json())
    .then((res) => {
      if (res.key !== undefined) {
        const token = res.key;
        localStorage.setItem("token", token);
        let cookiearray = document.cookie.split(";");
        let csrf = "";
        for (var i = 0; i < cookiearray.length; i++) {
          let name = cookiearray[i].split("=")[0];
          let value = cookiearray[i].split("=")[1];
          if (name === " csrftoken") {
            csrf = value;
          }
        }
        localStorage.setItem("csrf", csrf);
        dispatch(authSuccess(token));
        dispatch(fetchProfile());
        dispatch(fetchMyCourses());
        dispatch(fetchMyProjects());
        dispatch(fetchMyReferrals());
        onSuccessLogin(pathname);
      } else {
        var error = new Error("Error " + res.status);
        error.res = res;
        throw error;
      }
    })
    .catch((err) => {
      if (err.res) {
        err.res.json().then((res) => {
          let msg = res.non_field_errors ? res.non_field_errors[0] : null;
          dispatch(authFail(msg));
          onFailedLogin();
        });
      }
    });
};

