import * as actionTypes from "../actions/actionTypes";
import { updateObejct } from "../utility";

const initialState = {
  token: null,
  error: null,
  loading: false,
  signup: false,
  msg: null,
};

const authStart = (state) => {
  return updateObejct(state, {
    error: null,
    loading: true,
    msg: null,
    signup: false,
  });
};

const authSuccess = (state, action) => {
  return updateObejct(state, {
    token: action.token,
    loading: false,
    error: null,
    msg: null,
    signup: false,
  });
};

const authFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    loading: false,
    msg: null,
    signup: false,
    token: null,
  });
};

const authLogout = (state) => {
  return updateObejct(state, {
    token: null,
    msg: null,
    signup: false,
  });
};

const signupSuccess = (state, action) => {
  return updateObejct(state, {
    signup: true,
    msg: action.msg,
    loading: false,
  });
};

const signupFail = (state, action) => {
  return updateObejct(state, {
    signup: false,
    error: action.error,
    loading: false,
  });
};

const clearMsg = (state, action) => {
  return updateObejct(state, {
    msg: null,
    error: null,
  });
};

const forgotSuccess = (state, action) => {
  console.log("forgot success");
  return updateObejct(state, {
    msg: action.msg,
  });
};

const forgotFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
  });
};

const resetSuccess = (state, action) => {
  console.log("forgot success");
  return updateObejct(state, {
    msg: action.msg,
  });
};

const resetFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
  });
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.AUTH_CLEARMSG:
      return clearMsg(state, action);
    case actionTypes.AUTH_START:
      return authStart(state, action);
    case actionTypes.AUTH_SUCCESS:
      return authSuccess(state, action);
    case actionTypes.AUTH_FAIL:
      return authFail(state, action);
    case actionTypes.AUTH_LOGOUT:
      return authLogout(state, action);
    case actionTypes.SIGNUP_SUCCESS:
      return signupSuccess(state, action);
    case actionTypes.SIGNUP_FAIL:
      return signupFail(state, action);
    case actionTypes.FORGOT_SUCCESS:
      return forgotSuccess(state, action);
    case actionTypes.FORGOT_FAIL:
      return forgotFail(state, action);
    case actionTypes.RESET_SUCCESS:
      return resetSuccess(state, action);
    case actionTypes.RESET_FAIL:
      return resetFail(state, action);
    default:
      return state;
  }
};

export default authReducer;
