import * as actionTypes from "../actions/actionTypes";
import { updateObejct } from "../utility";

const initialState = {
  error: null,
  myCoursesLoading: false,
  myCourses: [],
  allCoursesLoading: false,
  allCourses: [],
  courseDataLoading: false,
  courseData: {},
  quizDataLoading: false,
  quizData: {},
  quizResponse: {},
  moduleData: {}, // contains module data having content list
  slots: [],
};

const myCoursesStart = (state) => {
  return updateObejct(state, {
    error: null,
    myCoursesLoading: true,
  });
};

const myCoursesFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    myCoursesLoading: false,
  });
};

const myCoursesSuccess = (state, action) => {
  return updateObejct(state, {
    myCoursesLoading: false,
    error: null,
    myCourses: action.myCourses,
  });
};

const allCoursesStart = (state) => {
  return updateObejct(state, {
    error: null,
    allCoursesLoading: true,
  });
};

const allCoursesFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    allCoursesLoading: false,
  });
};

const allCoursesSuccess = (state, action) => {
  return updateObejct(state, {
    allCoursesLoading: false,
    error: null,
    allCourses: action.allCourses,
  });
};

const courseDataStart = (state) => {
  return updateObejct(state, {
    error: null,
    courseDataLoading: true,
  });
};

const courseDataFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    courseDataLoading: false,
  });
};

const courseDataSuccess = (state, action) => {
  console.log(action);
  return updateObejct(state, {
    courseDataLoading: false,
    error: null,
    courseData: action.courseData,
  });
};

const moduleDataFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
  });
};

const moduleDataSuccess = (state, action) => {
  return updateObejct(state, {
    error: null,
    moduleData: action.moduleData,
  });
};

const quizDataStart = (state) => {
  return updateObejct(state, {
    error: null,
    courseDataLoading: true,
  });
};

const quizDataFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    courseDataLoading: false,
  });
};

const quizDataSuccess = (state, action) => {
  return updateObejct(state, {
    courseDataLoading: false,
    error: null,
    quizData: action.quizData,
  });
};

const responseSuccess = (state, action) => {
  return updateObejct(state, {
    quizResponse: action.quizResponse,
  });
};

const responseFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
  });
};

const progressSuccess = (state, action) => {
  let courseData = state.courseData;
  let progress = courseData.additional_info.progress;
  console.log("progress before ", progress);
  if (progress[action.module_slug]) {
    progress[action.module_slug][action.content_slug] = true;
  } else {
    progress[action.module_slug] = {
      [action.content_slug]: true,
    };
  }
  console.log("progress after ", progress);
  courseData.additional_info.progress = progress;
  return updateObejct(state, {
    courseData: courseData,
  });
};

const progressFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
  });
};

const createThread = (state, action) => {
  let moduleData = { ...state.moduleData };
  let threads = moduleData.threads;
  threads.push(action.thread);
  return updateObejct(state, {
    moduleData: moduleData,
  });
};

const followUpSuccess = (state, action) => {
  let moduleData = { ...state.moduleData };
  let threads = moduleData.threads;
  let index = threads.findIndex((item) => item.id === action.thread_id);
  if (index !== -1) {
    threads[index].followups.push(action.followUp);
  }
  return updateObejct(state, {
    moduleData: moduleData,
  });
};

const slotsFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
  });
};

const slotsSuccess = (state, action) => {
  return updateObejct(state, {
    error: null,
    slots: action.slots,
  });
};

const courseReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.MYCOURSES_START:
      return myCoursesStart(state, action);
    case actionTypes.MYCOURSES_FAIL:
      return myCoursesFail(state, action);
    case actionTypes.MYCOURSES_SUCCESS:
      return myCoursesSuccess(state, action);
    case actionTypes.ALLCOURSES_START:
      return allCoursesStart(state, action);
    case actionTypes.ALLCOURSES_FAIL:
      return allCoursesFail(state, action);
    case actionTypes.ALLCOURSES_SUCCESS:
      return allCoursesSuccess(state, action);
    case actionTypes.COURSEDATA_START:
      return courseDataStart(state, action);
    case actionTypes.COURSEDATA_FAIL:
      return courseDataFail(state, action);
    case actionTypes.COURSEDATA_SUCCESS:
      return courseDataSuccess(state, action);
    case actionTypes.MODULEDATA_FAIL:
      return moduleDataFail(state, action);
    case actionTypes.MODULEDATA_SUCCESS:
      return moduleDataSuccess(state, action);
    case actionTypes.QUIZDATA_START:
      return quizDataStart(state, action);
    case actionTypes.QUIZDATA_FAIL:
      return quizDataFail(state, action);
    case actionTypes.QUIZEDATA_SUCCESS:
      return quizDataSuccess(state, action);
    case actionTypes.QUIZRESPONSE_FAIL:
      return responseFail(state, action);
    case actionTypes.QUIZRESPONSE_SUCCESS:
      return responseSuccess(state, action);
    case actionTypes.PROGRESS_FAIL:
      return progressFail(state, action);
    case actionTypes.PROGRESS_SUCCESS:
      return progressSuccess(state, action);
    case actionTypes.CREATE_THREAD_SUCCESS:
      return createThread(state, action);
    case actionTypes.FOLLOWUP_SUCCESS:
      return followUpSuccess(state, action);
    case actionTypes.SLOTS_SUCCESS:
      return slotsSuccess(state, action);
    case actionTypes.SLOTS_FAIL:
      return slotsFail(state, action);
    case actionTypes.MYSLOTS_SUCCESS:
    default:
      return state;
  }
};

export default courseReducer;
