import * as actionTypes from "../actions/actionTypes";
import { updateObejct } from "../utility";

const initialState = {
  error: null,
  msg: null,
};

const successMsg = (state, action) => {
  return updateObejct(state, {
    msg: action.msg,
    error: null,
  });
};

const failMsg = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    msg: null,
  });
};

const clearMsg = (state, action) => {
  console.log("clearrringggg");
  return updateObejct(state, {
    error: null,
    msg: null,
  });
};

const msgReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SUCCESS_MSG:
      return successMsg(state, action);
    case actionTypes.FAIL_MSG:
      return failMsg(state, action);
    case actionTypes.CLEAR_MSG:
      return clearMsg(state, action);
    default:
      return state;
  }
};

export default msgReducer;
