import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Footer from "./Footer/Footer";
import { Box, Button, Grid, TextField, Typography } from "@material-ui/core";
import { useFormik } from "formik";
import * as Yup from "yup";

//SVG's here
import ContactUsBackground from "../public/images/contactUs-bg.svg";
import FacebookLogo from "../public/images/contactUs-facebook.svg";
import InstagramLogo from "../public/images/contactUs-instagram.svg";
import LinkedinLogo from "../public/images/contactUs-linkedin.svg";
import YoutubeLogo from "../public/images/contactUs-youtube.svg";

const PINK = "#e03268";
const FACEBOOK = "https://www.facebook.com/robofreaks.official";
const INSTAGRAM = "https://www.facebook.com/robofreaks.official";
const LINKEDIN = "https://www.linkedin.com/company/robofreaks/";
const YOUTUBE = "https://www.youtube.com/channel/UCu24I8hkuDwZJzdQ4CwCa2Q/";

const useStyles = makeStyles((theme) => ({
  root: {
    top: "10vh",
    position: "relative",
    backgroundColor: "#f9f9f9",
    "& .MuiOutlinedInput-root": {
      borderRadius: "22px",
      backgroundColor: "#ffffff",
      "&.Mui-focused fieldset": {
        borderColor: PINK,
      },
    },
    "& .MuiInputBase-input": {
      minHeight: 0,
      height: "auto",
    },
    "& .MuiInputBase-inputMultiline": {
      minHeight: "200px",
    },
    "& .MuiButton-root:hover": {
      backgroundColor: "inherit",
      color: "#000000",
      borderColor: PINK,
      border: "solid 1px",
    },
    "& .MuiFormLabel-root": {
      fontWeight: "bold",
      fontSize: ".75rem",
      minWidth: 0,
    },
  },
  boxStyle: {
    minHeight: "90vh",
    background: `url(${ContactUsBackground})`,
    backgroundRepeat: "no-repeat",
    backgroundPosition: "right",
    backgroundSize: "contain",
    backgroundPositionY: 0,
    [theme.breakpoints.down("sm")]: {
      background: "none",
      backgroundColor: "#f9f9f9",
      margin: theme.spacing(2),
      marginTop: 0,
    },
  },
  contactDiv: {
    marginLeft: theme.spacing(20),
    maxWidth: "600px",
    [theme.breakpoints.down("sm")]: {
      margin: "auto",
      textAlign: "center",
    },
  },
  contactHeading: {
    marginBottom: theme.spacing(3),
    fontWeight: "bold",
    fontSize: "6em",
    [theme.breakpoints.down("md")]: {
      fontSize: "5em",
    },
    [theme.breakpoints.down("sm")]: {
      fontSize: "3.5em",
    },
  },
  formTextInputs: {
    marginBottom: theme.spacing(4),
    width: "100%",
  },
  submitButton: {
    width: "100%",
    backgroundColor: PINK,
    borderRadius: "22px",
    color: "#ffffff",
    fontWeight: "bold",
    minHeight: "57px",
  },
  checkSocialsHead: {
    textAlign: "center",
    fontWeight: "bold",
    fontSize: "3em",
    marginTop: "20px",
    [theme.breakpoints.down("md")]: {
      fontSize: "2em",
    },
    [theme.breakpoints.down("sm")]: {
      fontSize: "1.5em",
    },
  },
  socialLinks: {
    height: "50px",
    width: "auto",
    margin: "20px",
    [theme.breakpoints.down("sm")]: {
      height: "30px",
    },
  },
  linkEffect: {
    transition: "all .1s ease-in-out",
    "&:hover": {
      transform: "scale(1.1,1.1)",
    },
  },
}));

export default function Contact() {
  const classes = useStyles();
  // formik --start--
  const formik = useFormik({
    initialValues: {
      firstName: "",
      lastName: "",
      email: "",
      message: "",
    },
    validationSchema: Yup.object({
      firstName: Yup.string()
        .min(3, "Must be 3 characters or more")
        .max(10, "Must be 10 characters or less")
        .nullable()
        .required("Required"),
      lastName: Yup.string()
        .min(3, "Must be 3 characters or more")
        .max(10, "Must be 10 characters or less")
        .nullable()
        .required("Required"),
      email: Yup.string()
        .email("Invalid email address")
        .nullable()
        .required("Required"),
      message: Yup.string()
        .min(10, "Must be 10 characters or more")
        .nullable()
        .required("Required"),
    }),
    onSubmit: (values) => {
      alert(JSON.stringify(values, null, 2));
    },
  });
  // formik --end--
  return (
    <div className={classes.root}>
      <Box display="flex" alignItems="center" className={classes.boxStyle}>
        <div className={classes.contactDiv}>
          <Typography className={classes.contactHeading}>Contact Us</Typography>
          <form onSubmit={formik.handleSubmit}>
            <Grid container direction="column" justify="center">
              <Grid
                item
                container
                direction="row"
                justify="space-between"
                alignItems="center"
                spacing={1}
              >
                <Grid item sm={6} xs={12}>
                  <TextField
                    id="firstName"
                    label="First Name"
                    variant="outlined"
                    className={classes.formTextInputs}
                    helperText={
                      formik.touched.firstName &&
                      formik.values.firstName &&
                      formik.errors.firstName
                        ? formik.errors.firstName
                        : null
                    }
                    {...formik.getFieldProps("firstName")}
                  />
                </Grid>
                <Grid item sm={6} xs={12}>
                  <TextField
                    id="lastName"
                    label="Last Name"
                    variant="outlined"
                    className={classes.formTextInputs}
                    helperText={
                      formik.touched.lastName &&
                      formik.values.lastName &&
                      formik.errors.lastName
                        ? formik.errors.lastName
                        : null
                    }
                    {...formik.getFieldProps("lastName")}
                  />
                </Grid>
              </Grid>
              <Grid item>
                <TextField
                  id="email"
                  label="Email"
                  variant="outlined"
                  fullWidth
                  className={classes.formTextInputs}
                  helperText={
                    formik.touched.email &&
                    formik.values.email &&
                    formik.errors.email
                      ? formik.errors.email
                      : null
                  }
                  {...formik.getFieldProps("email")}
                />
              </Grid>
              <Grid item>
                <TextField
                  id="message"
                  label="Message"
                  variant="outlined"
                  fullWidth
                  multiline
                  className={classes.formTextInputs}
                  helperText={
                    formik.touched.message &&
                    formik.values.message &&
                    formik.errors.message
                      ? formik.errors.message
                      : null
                  }
                  {...formik.getFieldProps("message")}
                />
              </Grid>
              <Grid item sm={6} xs={12}>
                <Button className={classes.submitButton} type="submit">
                  Submit
                </Button>
              </Grid>
            </Grid>
          </form>
        </div>
      </Box>
      <Typography className={classes.checkSocialsHead}>
        Check out our Social Handles for more updates!
      </Typography>
      <Box
        display="flex"
        flexWrap="wrap"
        justifyContent="center"
        style={{ marginBottom: "40px" }}
      >
        <a
          className={classes.linkEffect}
          href={FACEBOOK}
          rel="noreferrer"
          target="_blank"
        >
          <img alt="" className={classes.socialLinks} src={FacebookLogo} />
        </a>
        <a
          className={classes.linkEffect}
          href={INSTAGRAM}
          rel="noreferrer"
          target="_blank"
        >
          <img alt="" className={classes.socialLinks} src={InstagramLogo} />
        </a>
        <a
          className={classes.linkEffect}
          href={LINKEDIN}
          rel="noreferrer"
          target="_blank"
        >
          <img alt="" className={classes.socialLinks} src={LinkedinLogo} />
        </a>
        <a
          className={classes.linkEffect}
          href={YOUTUBE}
          rel="noreferrer"
          target="_blank"
        >
          <img alt="" className={classes.socialLinks} src={YoutubeLogo} />
        </a>
      </Box>
      <Footer />
    </div>
  );
}
