export const threadsStart = () => ({
  type: actionTypes.THREADS_START,
});

export const threadsFail = (error) => ({
  type: actionTypes.THREADS_FAIL,
  error: error,
});

export const threadsSuccess = (threads) => ({
  type: actionTypes.THREADS_SUCCESS,
  threads: threads,
});

export const fetchThreads = (Module, success, failed) => (dispatch) => {
  dispatch(threadsStart());

  const token = localStorage.getItem("token");
  // let cookiearray = document.cookie.split(";");
  // let csrf = "";
  // for (var i = 0; i < cookiearray.length; i++) {
  //   let name = cookiearray[i].split("=")[0];
  //   let value = cookiearray[i].split("=")[1];
  //   if (name === " csrftoken") {
  //     csrf = value;
  //   }
  // }
  // console.log(csrf);

  // let csrf = localStorage.getItem('csrf');
  let csrf = "";
  if (token) {
    csrf = localStorage.getItem("csrf");
  }

  return fetch(config.url.API_URL + `/forum/module/${Module}`, {
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      Authorization: "Token " + token,
      "X-CSRFToken": csrf,
    },
  })
    .then(
      (response) => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error(
            "Error " + response.status + ": " + response.statusText
          );
          error.response = response;
          throw error;
        }
      },
      (error) => {
        var errmess = new Error(error.message);
        throw errmess;
      }
    )
    .then((response) => response.json())
    .then((threads) => {
      dispatch(threadsSuccess(threads));
      success();
    })
    .catch((error) => {
      if (error.response) {
        error.response.json((error) => {
          dispatch(threadsFail(error.detail));
          failed();
        });
      }
    });
};

