export const createSuccess = (thread) => ({
  type: actionTypes.CREATE_THREAD_SUCCESS,
  thread: thread,
});

export const createThread = (
  module_slug,
  title,
  description,
  images,
  additional_info,
  slot,
  successCreate,
  slotBooked
) => (dispatch) => {
  const token = localStorage.getItem("token");
  let csrf = "";
  if (token) {
    csrf = localStorage.getItem("csrf");
  }

  const formData = new FormData();
  if (title) {
    formData.append("title", title);
  }
  if (description) {
    formData.append("description", description);
  }
  if (module_slug) {
    formData.append("module", module_slug);
  }
  // formData.append("forum", Number(forum_id))
  let arr = [];
  if (images) {
    console.log(images);
    // formData.append("", images[0].name, images[0]);
    formData.append("image", images.file, images.title);
  }
  // if (additional_info) {
  formData.append("additional_info", "{}");
  // }

  if (slot) {
    console.log(slot);
    dispatch(bookSlot(slot, slotBooked));
  }

  return fetch(config.url.API_URL + `/forum/thread`, {
    method: "POST",
    headers: {
      Authorization: "Token " + token,
      "X-CSRFToken": csrf,
    },
    body: formData,
  })
    .then(
      (response) => {
        if (response.ok) {
          console.log(response);
          return response;
        } else {
          var error = new Error(
            "Error " + response.status + ": " + response.statusText
          );
          error.response = response;
          throw error;
        }
      },
      (error) => {
        var errmess = new Error(error.message);
        throw errmess;
      }
    )
    .then((response) => response.json())
    .then((thread) => {
      console.log("thread created");
      dispatch(createSuccess(thread));
      // successCreate(thread.id);
    })
    .catch((error) => {
      console.log(error);
      if (error.response) {
        error.response.json().then((res) => {
          console.log(res);
        });
      }
    });
};

export const followUpSuccess = (followUp, thread_id) => ({
  type: actionTypes.FOLLOWUP_SUCCESS,
  followUp: followUp,
  thread_id: thread_id,
});

export const followUp = (thread_id, description, images) => (dispatch) => {
  const token = localStorage.getItem("token");
  let csrf = "";
  if (token) {
    csrf = localStorage.getItem("csrf");
  }

  const formData = new FormData();
  if (description) {
    formData.append("description", description);
  }
  if (images) {
    // let arr = [];
    // for (let i = 0; i < images.length; ++i) {
    //   arr.push(images[i]);
    //   // formData.append(images[i].name, images[i]);
    // }
    formData.append("assets", images);
  }

  formData.append("thread", Number(thread_id));

  return fetch(config.url.API_URL + `/forum/followup`, {
    method: "POST",
    headers: {
      Authorization: "Token " + token,
      "X-CSRFToken": csrf,
    },
    body: formData,
  })
    .then(
      (response) => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error(
            "Error " + response.status + ": " + response.statusText
          );
          error.response = response;
          throw error;
        }
      },
      (error) => {
        var errmess = new Error(error.message);
        throw errmess;
      }
    )
    .then((response) => response.json())
    .then((followUp) => {
      dispatch(followUpSuccess(followUp, thread_id));
    })
    .catch((error) => {
      if (error.response) {
        error.response.json().then((res) => {});
      }
    });
};

