import * as actionTypes from "./actionTypes";

export const clear = () => ({
  type: actionTypes.CLEAR_MSG,
});

export const clearMsg = () => (dispatch) => {
  dispatch(clear());
};

export const set = (msg) => ({
  type: actionTypes.SUCCESS_MSG,
  msg: msg,
});

export const setMsg = (msg) => (dispatch) => {
  dispatch(set(msg));
  setTimeout(() => {
    dispatch(clearMsg());
  }, 7000);
};
