import * as actionTypes from "../actions/actionTypes";
import { updateObejct } from "../utility";

const initialState = {
  error: null,
  assignmentLoading: false,
  assignments: [],
};

const allAssignmentsStart = (state) => {
  return updateObejct(state, {
    error: null,
    assignmentLoading: true,
  });
};
const allAssignmentsSuccess = (state, action) => {
  return updateObejct(state, {
    error: null,
    assignmentLoading: false,
    assignments: action.assignments,
  });
};
const allAssignmentsFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    assignmentLoading: false,
  });
};

const assignmentReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.ASSIGNMENT_START:
      return allAssignmentsStart(state);
    case actionTypes.ASSIGNMENT_SUCCESS:
      return allAssignmentsSuccess(state, action);
    case actionTypes.ASSIGNMENT_FAIL:
      return allAssignmentsFail(state, action);
    default:
      return state;
  }
};

export default assignmentReducer;
