import React, { Component } from "react";
import { withRouter, Link, Redirect } from "react-router-dom";
import { withStyles } from "@material-ui/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import PropTypes from "prop-types";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import CompBook from "../../public/icons/refferalApply.svg";
import RoboFreaks from "../../public/robofreaks-logo.webp";
import Hidden from "@material-ui/core/Hidden";
import {
  CircularProgress,
  TextField,
  FormControl,
  InputLabel,
  OutlinedInput,
  InputAdornment,
  IconButton,
} from "@material-ui/core";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import GoogleSocialAuth from "../GoogleLogin";
import CloseRoundedIcon from "@material-ui/icons/CloseRounded";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import "./Login.css";
const PINK = "#e03268";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = (theme) => ({
  root: {
    width: 400,
    borderRadius: "10px",
  },
  bullet: {
    display: "inline-block",
    margin: "0 2px",
    transform: "scale(0.8)",
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  formStyle: {
    "& .MuiOutlinedInput-root": {
      borderRadius: "22px",
      backgroundColor: "#ffffff",
      "&.Mui-focused fieldset": {
        borderColor: PINK,
      },
    },
    "& .MuiInputBase-input": {
      minHeight: 0,
      height: "auto",
    },
    "& .MuiInputBase-inputMultiline": {
      minHeight: "200px",
    },
    "& .MuiFormLabel-root": {
      fontWeight: "bold",
      fontSize: ".75rem",
      minWidth: 0,
    },
  },
  formButtons: {
    height: "50px",
    borderRadius: "22px",
    fontWeight: "bold",
  },
});

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hidden: true,
      email: "",
      pass: "",
      username: "",
      forgot: false,
      loginSubmitted: false,
      forgotSubmitted: false,
      touched: {
        pass: false,
        email: false,
        username: false,
      },
    };

    this.toggleShow = this.toggleShow.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.onSuccessLogin = this.onSuccessLogin.bind(this);
    this.onFailedLogin = this.onFailedLogin.bind(this);
    this.googleCallback = this.googleCallback.bind(this);
    this.handleForgot = this.handleForgot.bind(this);
    this.onSuccessForgot = this.onSuccessForgot.bind(this);
    this.onFailForgot = this.onFailForgot.bind(this);

    this.handleInputChange = this.handleInputChange.bind(this);
    // this.handleSubmit = this.handleSubmit.bind(this);
    this.handleBlur = this.handleBlur.bind(this);
    // this.toggleShow1 = this.toggleShow1.bind(this);
    // this.toggleShow2 = this.toggleShow2.bind(this);
    // this.onSuccessSignup = this.onSuccessSignup.bind(this);
    // this.onFailedSignUp = this.onFailedSignUp.bind(this);
  }

  componentDidMount() {
    const navbar = document.getElementById("navbar");
    if (navbar) {
      navbar.classList.add("displaynone");
    }
  }

  componentDidUpdate() {
    const navbar = document.getElementById("navbar");
    if (navbar) {
      navbar.classList.add("displaynone");
    }
  }

  // componentWillUnmount() {
  //   const navbar = document.getElementById("navbar");
  //   if (navbar) {
  //     navbar.classList.remove("displaynone");
  //   }
  // }

  handleBlur = (field) => (evt) => {
    this.setState({
      touched: { ...this.state.touched, [field]: true },
    });
  };
  handleInputChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;
    this.setState({
      [name]: value,
    });
  }
  handleSubmit(event) {
    event.preventDefault();
    this.setState({ loginSubmitted: true });
    let pathname =
      this.props.prevLocation.pathname &&
      this.props.prevLocation.pathname !== "/login"
        ? this.props.prevLocation.pathname
        : "/";
    this.props.authLogin(
      this.state.username,
      this.state.pass,
      pathname,
      this.onSuccessLogin,
      this.onFailedLogin
    );
    // this.setState({
    //   username: values.username,
    // });
  }

  validate(email) {
    const errors = {
      email: "",
    };
    if (this.state.touched.email) {
      if (
        !/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
          email
        )
      )
        errors.email = "Invalid Email Address";
    }
    return errors;
  }

  toggleShow() {
    this.setState({ hidden: !this.state.hidden });
  }

  googleCallback(accesstoken) {
    this.props.googleLogin(
      accesstoken.accessToken,
      this.onSuccessLogin,
      this.onFailedLogin,
      this.props.prevLocation.pathname
    );
  }

  onSuccessGoogleSignUp() {
    this.props.history.push("/data");
  }

  onSuccessLogin(pathname) {
    this.setState({
      loginSubmitted: false,
    });
    if (
      pathname === undefined ||
      pathname === "/home" ||
      pathname === "/signup"
    ) {
      this.props.history.push("/profile");
    } else {
      this.props.history.push(pathname);
    }
  }

  onFailedLogin() {
    this.setState({
      loginSubmitted: false,
    });
    // this.props.history.push("/login");
  }

  handleForgot(event) {
    this.setState({
      forgotSubmitted: true,
    });
    this.props.forgotPass(
      this.state.email,
      this.onSuccessForgot,
      this.onFailForgot
    );
    event.preventDefault();
  }

  onSuccessForgot() {
    this.props.history.push("/");
    this.setState({
      forgotSubmitted: false,
    });
  }

  onFailForgot() {
    this.setState({
      forgotSubmitted: false,
    });
  }

  render() {
    const { classes } = this.props;
    const errors = this.validate(this.state.email);
    return (
      <>
        {this.props.msg.msg || this.props.msg.error ? (
          <Snackbar
            open={this.state.open}
            anchorOrigin={{
              vertical: "top",
              horizontal: "center",
            }}
          >
            <Alert
              onClose={() => {
                this.handleClose();
              }}
              severity={this.props.msg.msg ? "success" : "error"}
            >
              {this.props.msg.msg}
              {this.props.msg.error}
            </Alert>
          </Snackbar>
        ) : null}
        <div className="container-fluid mt-4">
          <Grid container justify="space-around">
            <Grid item xs={12} style={{ zIndex: "1", paddingBottom: "0" }}>
              <div
                style={{
                  color: "#fff",
                  display: "flex",
                  justifyContent: "center",
                }}
              >
                <img src={RoboFreaks} alt="robofreaks" height={40} width={40} />
                <a href="/home" style={{ textDecoration: "none" }}>
                  <Typography
                    variant="h5"
                    component="h2"
                    style={{
                      lineHeight: "41px",
                      cursor: "pointer",
                      color: "#fff",
                    }}
                  >
                    RoboFreaks
                  </Typography>
                </a>
              </div>
            </Grid>
            <Hidden smDown>
              <Grid item style={{ zIndex: "1", paddingTop: "0" }}>
                <img
                  alt="CompBook"
                  src={CompBook}
                  height={500}
                  width={500}
                  style={{ filter: "drop-shadow(5px 5px 5px #404040)" }}
                />
              </Grid>
            </Hidden>
            <Grid item style={{ zIndex: "1", paddingTop: "2.5em" }}>
              <Typography
                variant="h6"
                gutterBottom
                style={{ color: "#fff", textAlign: "center" }}
              >
                Welcome to ROBOFREAKS
              </Typography>
              {this.state.forgot ? (
                <Card className={classes.root} variant="outlined" elevation={5}>
                  <CardContent className={classes.formStyle}>
                    <div style={{ display: "flex", flex: "row" }}>
                      <Typography variant="h4" gutterBottom>
                        Forgot Password?
                      </Typography>
                      <Link
                        onClick={() => {
                          this.setState({
                            forgot: false,
                          });
                        }}
                        className="ml-auto"
                        style={{ color: "black" }}
                      >
                        <CloseRoundedIcon />
                        {/* <span className="fa fa-times fa-lg"></span> */}
                      </Link>
                    </div>
                    <hr />
                    <form className="forgotForm" onSubmit={this.handleForgot}>
                      <div className="form-group">
                        <label htmlFor="email">
                          Enter you email address below and we will send you a
                          link to reset your password
                        </label>
                        <TextField
                          fullWidth
                          type="text"
                          id="email"
                          label="Email"
                          name="email"
                          variant="outlined"
                          value={this.state.email}
                          valid={errors.email === ""}
                          invalid={errors.email !== ""}
                          onBlur={this.handleBlur("email")}
                          onChange={this.handleInputChange}
                          helperText={errors.email}
                        />
                      </div>
                      <div className="row form-group">
                        <div className="col col-12">
                          <Button
                            className={classes.formButtons}
                            variant="contained"
                            type="submit"
                            fullWidth={true}
                            style={{
                              backgroundColor: "#09bfad",
                              color: "white",
                              outline: "none",
                              height: 50,
                            }}
                          >
                            {this.state.forgotSubmitted ? (
                              <CircularProgress color="inherit" />
                            ) : (
                              "Submit"
                            )}
                          </Button>
                        </div>
                      </div>
                    </form>
                  </CardContent>
                </Card>
              ) : !localStorage.getItem("token") ? (
                <Card className={classes.root} variant="outlined" elevation={5}>
                  <CardContent className={classes.formStyle}>
                    <Typography variant="h4" gutterBottom>
                      Login
                    </Typography>
                    <hr />
                    <div className="border-bottom mb-3">
                      {/* <FacebookSocialAuth facebookCallback={this.facebookCallback} /> */}
                      <GoogleSocialAuth googleCallback={this.googleCallback} />
                    </div>
                    <form className="loginForm" onSubmit={this.handleSubmit}>
                      <div className="form-group">
                        <TextField
                          fullWidth
                          type="text"
                          id="username"
                          label="Email or Username"
                          name="username"
                          variant="outlined"
                          value={this.state.username}
                          valid={errors.username === ""}
                          invalid={errors.username !== ""}
                          onBlur={this.handleBlur("username")}
                          onChange={this.handleInputChange}
                          helperText={errors.username}
                        />
                      </div>
                      <div className="form-group">
                        <FormControl variant="outlined" fullWidth>
                          <InputLabel htmlFor="outlined-adornment-password">
                            Password
                          </InputLabel>
                          <OutlinedInput
                            id="outlined-adornment-password"
                            name="pass"
                            type={this.state.hidden ? "password" : "text"}
                            value={this.state.pass}
                            onChange={this.handleInputChange}
                            onBlur={this.handleBlur("pass")}
                            valid={errors.pass === ""}
                            invalid={errors.pass !== ""}
                            endAdornment={
                              <InputAdornment position="end">
                                <IconButton
                                  style={{ outline: "none" }}
                                  aria-label="toggle password visibility"
                                  onClick={this.toggleShow}
                                  onMouseDown={this.toggleShow}
                                  edge="end"
                                >
                                  {this.state.hidden ? (
                                    <Visibility />
                                  ) : (
                                    <VisibilityOff />
                                  )}
                                </IconButton>
                              </InputAdornment>
                            }
                            labelWidth={70}
                          />
                        </FormControl>
                      </div>
                      <div className="row form-group text-muted">
                        <div className="ml-auto">
                          <Button
                            style={{ color: "#007bff" }}
                            onClick={() =>
                              this.setState({ forgot: !this.state.forgot })
                            }
                            variant="text"
                          >
                            <small>Forgot Password?</small>
                          </Button>
                        </div>
                      </div>
                      <div className="row form-group">
                        <div className="col col-12">
                          <Button
                            className={classes.formButtons}
                            variant="contained"
                            type="submit"
                            fullWidth={true}
                            style={{
                              backgroundColor: "#09bfad",
                              color: "white",
                              outline: "none",
                              height: 50,
                            }}
                          >
                            {this.state.loginSubmitted ? (
                              <CircularProgress color="inherit" />
                            ) : (
                              "Login"
                            )}
                          </Button>
                        </div>
                      </div>
                    </form>
                    <div className="border-top pt-3">
                      <div className="text-muted">
                        Need An Account?&nbsp;
                        <Link
                          to={{
                            pathname: "/signup",
                            // state: {
                            //   modal: true,
                            //   redirected: this.props.redirected,
                            // },
                          }}
                        >
                          SignUp
                        </Link>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ) : (
                <Redirect to="/home" />
              )}
            </Grid>
          </Grid>
        </div>
        <div className="refferaldiv">
          <div className="child">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
              <path
                fill="#fff"
                fill-opacity="1"
                d="M0,256L40,261.3C80,267,160,277,240,272C320,267,400,245,480,197.3C560,149,640,75,720,58.7C800,43,880,85,960,101.3C1040,117,1120,107,1200,85.3C1280,64,1360,32,1400,16L1440,0L1440,320L1400,320C1360,320,1280,320,1200,320C1120,320,1040,320,960,320C880,320,800,320,720,320C640,320,560,320,480,320C400,320,320,320,240,320C160,320,80,320,40,320L0,320Z"
              ></path>
            </svg>
          </div>
        </div>
        <br />
        <br />
      </>
    );
  }
}
Login.propTypes = {
  classes: PropTypes.object.isRequired,
};
export default withStyles(useStyles)(withRouter(Login));
