import * as actionTypes from "../actions/actionTypes";
import { updateObejct } from "../utility";

const initialState = {
  error: null,
  loading: false,
  threads: [],
  threadDetail: {},
  slots: [],
  mySlots: [],
};

const threadsStart = (state) => {
  return updateObejct(state, {
    error: null,
    loading: true,
  });
};

const threadsFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    loading: false,
  });
};

const threadsSuccess = (state, action) => {
  return updateObejct(state, {
    loading: false,
    error: null,
    threads: action.threads,
  });
};

const threadDetailStart = (state, action) => {
  return updateObejct(state, {
    error: null,
    loading: true,
  });
};

const threadDetailFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    loading: false,
  });
};

const threadDetailSuccess = (state, action) => {
  return updateObejct(state, {
    loading: false,
    error: null,
    threadDetail: action.threadDetail,
  });
};

const slotsFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    loading: false,
  });
};

const slotsSuccess = (state, action) => {
  return updateObejct(state, {
    loading: false,
    error: null,
    slots: action.slots,
  });
};

const mySlotsFail = (state, action) => {
  return updateObejct(state, {
    error: action.error,
    loading: false,
  });
};

const mySlotsSuccess = (state, action) => {
  return updateObejct(state, {
    loading: false,
    error: null,
    mySlots: action.mySlots,
  });
};

const createSuccess = (state, action) => {
  let threads = state.threads;
  threads.push(action.thread);
  return updateObejct(state, {
    threads: threads,
  });
};

const folloupSuccess = (state, action) => {
  let threadDetail = state.threadDetail;
  threadDetail.followups.push(action.followUp);
  return updateObejct(state, {
    threadDetail: threadDetail,
  });
};

const forumReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.THREADS_START:
      return threadsStart(state, action);
    case actionTypes.THREADS_FAIL:
      return threadsFail(state, action);
    case actionTypes.THREADS_SUCCESS:
      return threadsSuccess(state, action);
    // case actionTypes.CREATE_SUCCESS:
    //   return createSuccess(state, action);
    // case actionTypes.FOLLOWUP_SUCCESS:
    //   return folloupSuccess(state, action);
    case actionTypes.THREAD_DETAIL_START:
      return threadDetailStart(state, action);
    case actionTypes.THREAD_DETAIL_SUCCESS:
      return threadDetailSuccess(state, action);
    case actionTypes.THREAD_DETAIL_FAIL:
      return threadDetailFail(state, action);
    // case actionTypes.SLOTS_SUCCESS:
    //   return slotsSuccess(state, action);
    // case actionTypes.SLOTS_FAIL:
    //   return slotsFail(state, action);
    // case actionTypes.MYSLOTS_SUCCESS:
      return mySlotsSuccess(state, action);
    case actionTypes.MYSLOTS_FAIL:
      return mySlotsFail(state, action);
    default:
      return state;
  }
};

export default forumReducer;
