import React, { Component } from "react";
import { withRouter, Link } from "react-router-dom";
import { withStyles } from "@material-ui/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import PropTypes from "prop-types";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import CompBook from "../../public/icons/refferalApply.svg";
import RoboFreaks from "../../public/robofreaks-logo.webp";
import Hidden from "@material-ui/core/Hidden";
import {
  CircularProgress,
  FormControl,
  InputLabel,
  InputAdornment,
  OutlinedInput,
  IconButton,
  TextField,
} from "@material-ui/core";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import GoogleSocialAuth from "../GoogleLogin";
import "./Signup.css";
const PINK = "#e03268";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = (theme) => ({
  root: {
    minWidth: 275,
    borderRadius: "10px",
  },
  bullet: {
    display: "inline-block",
    margin: "0 2px",
    transform: "scale(0.8)",
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  formStyle: {
    "& .MuiOutlinedInput-root": {
      borderRadius: "22px",
      backgroundColor: "#ffffff",
      "&.Mui-focused fieldset": {
        borderColor: PINK,
      },
    },
    "& .MuiInputBase-input": {
      minHeight: 0,
      height: "auto",
    },
    "& .MuiInputBase-inputMultiline": {
      minHeight: "200px",
    },
    "& .MuiFormLabel-root": {
      fontWeight: "bold",
      fontSize: ".75rem",
      minWidth: 0,
    },
  },
  formButtons: {
    height: "50px",
    borderRadius: "22px",
    fontWeight: "bold",
  },
});

class SignUp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      fname: "",
      lname: "",
      pass1: "",
      pass2: "",
      username: "",
      studentgrade: 2,
      touched: {
        fname: false,
        lname: false,
        pass1: false,
        pass2: false,
        email: false,
        username: false,
        number: false,
      },
      hidden1: true,
      hidden2: true,
      submitted: false,
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleBlur = this.handleBlur.bind(this);
    this.toggleShow1 = this.toggleShow1.bind(this);
    this.toggleShow2 = this.toggleShow2.bind(this);
    this.onSuccessSignup = this.onSuccessSignup.bind(this);
    this.onFailedSignUp = this.onFailedSignUp.bind(this);
    this.googleCallback = this.googleCallback.bind(this);
    this.onSuccessGoogleSignUp = this.onSuccessGoogleSignUp.bind(this);
  }

  componentDidMount() {
    const navbar = document.getElementById("navbar");
    if (navbar) {
      navbar.classList.add("displaynone");
    }
  }

  componentDidUpdate() {
    const navbar = document.getElementById("navbar");
    if (navbar) {
      navbar.classList.add("displaynone");
    }
  }

  componentWillUnmount() {
    const navbar = document.getElementById("navbar");
    if (navbar) {
      navbar.classList.remove("displaynone");
    }
  }

  handleBlur = (field) => (evt) => {
    this.setState({
      touched: { ...this.state.touched, [field]: true },
    });
  };
  handleInputChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;
    this.setState({
      [name]: value,
    });
  }
  onSuccessSignup() {
    this.setState({ submitted: false });
    this.props.history.push("/data");
  }
  onFailedSignUp() {
    this.setState({ submitted: false });
  }
  handleSubmit(event) {
    this.setState({ submitted: true });
    // let code = this.props.history.location.pathname.split("/")[2];
    this.props.authSignup(
      this.state.username,
      this.state.email,
      this.state.pass1,
      this.state.fname,
      this.state.lname,
      null,
      this.onSuccessSignup,
      this.onFailedSignUp
    );
    // alert("Current State is: " + JSON.stringify(this.state));
    // this.setState({
    //   email: "",
    //   question: "",
    //   fname: "",
    //   lname: "",
    //   studentgrade: 2,
    // });
    // window.open("/", "_self");
    event.preventDefault();
  }
  validate(fname, lname, pass1, email, pass2, username) {
    const errors = {
      fname: "",
      lname: "",
      pass1: "",
      email: "",
      pass2: "",
      username: "",
    };

    if (this.state.touched.fname && fname.length < 3) {
      errors.fname = "First Name should be >= 3 characters";
    } else if (this.state.touched.fname && fname.length > 10)
      errors.fname = "First Name should be <= 15 characters";

    if (this.state.touched.lname && lname.length < 3)
      errors.lname = "Last Name should be >= 3 characters";
    else if (this.state.touched.lname && lname.length > 10)
      errors.lname = "Last Name should be <= 15 characters";

    // if (this.state.touched.parentName && parentName.length < 3)
    //   errors.parentName = "Parent Name should be >= 3 characters";
    // else if (this.state.touched.parentName && parentName.length > 10)
    //   errors.parentName = "Parent Name should be <= 10 characters";

    if (this.state.touched.email) {
      if (
        !/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
          email
        )
      )
        errors.email = "Invalid Email Address";
    }

    if (this.state.touched.username) {
      if (!/^[a-zA-Z0-9_]+$/i.test(username))
        errors.username = "Invalid Username";
    }

    if (this.state.touched.pass1) {
      if (
        !/^(?=.*[a-z])(?=.*\d)(?=.*[$@$!%*#?&<>^()])[a-z\d$@$!%*#?&<>^()]{8,}$/i.test(
          pass1
        )
      )
        errors.pass1 = "Invalid password";
    }

    if (this.state.touched.pass2) {
      if (this.state.pass1 !== pass2) errors.pass2 = "password does not match";
    }

    return errors;
  }

  toggleShow1() {
    this.setState({ hidden1: !this.state.hidden1 });
  }

  toggleShow2() {
    this.setState({ hidden2: !this.state.hidden2 });
  }

  googleCallback(accesstoken) {
    this.props.googleLogin(
      accesstoken.accessToken,
      this.onSuccessGoogleSignUp,
      this.onFailedLogin
    );
  }

  onSuccessGoogleSignUp() {
    this.props.history.push("/data");
  }

  // onSuccessLogin(pathname) {
  //   if (pathname === "/home" || pathname === undefined) {
  //     this.props.history.push("/profile");
  //   } else {
  //     this.props.history.push(pathname);
  //   }
  // }

  onFailedLogin() {
    // this.props.history.push("/signup");
  }

  render() {
    const { classes } = this.props;
    const errors = this.validate(
      this.state.fname,
      this.state.lname,
      this.state.pass1,
      this.state.email,
      this.state.pass2,
      this.state.username
    );
    return (
      <>
        {this.props.msg.msg || this.props.msg.error ? (
          <Snackbar
            open={this.state.open}
            anchorOrigin={{
              vertical: "top",
              horizontal: "center",
            }}
            // onClose={() => {
            //   this.handleClose();
            // }}
          >
            <Alert
              onClose={() => {
                this.handleClose();
              }}
              severity={this.props.msg.msg ? "success" : "error"}
            >
              {this.props.msg.msg}
              {this.props.msg.error}
            </Alert>
          </Snackbar>
        ) : null}
        <div className="container-fluid mt-4">
          <Grid container justify="space-around">
            <Grid item xs={12} style={{ zIndex: "1", paddingBottom: "0" }}>
              <div
                style={{
                  color: "#fff",
                  display: "flex",
                  justifyContent: "center",
                }}
              >
                <img src={RoboFreaks} alt="robofreaks" height={40} width={40} />
                <a href="/home" style={{ textDecoration: "none" }}>
                  <Typography
                    variant="h5"
                    component="h2"
                    style={{
                      lineHeight: "41px",
                      cursor: "pointer",
                      color: "#fff",
                    }}
                  >
                    RoboFreaks
                  </Typography>
                </a>
              </div>
            </Grid>
            <Hidden smDown>
              <Grid item style={{ zIndex: "1", paddingTop: "0" }}>
                <img
                  alt="CompBook"
                  src={CompBook}
                  height={500}
                  width={500}
                  style={{ filter: "drop-shadow(5px 5px 5px #404040)" }}
                />
              </Grid>
            </Hidden>
            <Grid item style={{ zIndex: "1", paddingTop: "2.5em" }}>
              <Typography
                variant="h6"
                gutterBottom
                style={{ color: "#fff", textAlign: "center" }}
              >
                Welcome to ROBOFREAKS
              </Typography>
              <Card className={classes.root} variant="outlined" elevation={5}>
                <CardContent className={classes.formStyle}>
                  <Typography variant="h4" gutterBottom>
                    SignUp
                  </Typography>
                  <hr />
                  <div className="border-bottom mb-3">
                    {/* <FacebookSocialAuth facebookCallback={this.facebookCallback} /> */}
                    <GoogleSocialAuth googleCallback={this.googleCallback} />
                  </div>
                  <form onSubmit={this.handleSubmit}>
                    <div className="row">
                      <div className="form-group col-6">
                        <TextField
                          fullWidth
                          type="text"
                          id="fname"
                          label="First Name"
                          name="fname"
                          variant="outlined"
                          value={this.state.fname}
                          valid={errors.fname === ""}
                          invalid={errors.fname !== ""}
                          onBlur={this.handleBlur("fname")}
                          onChange={this.handleInputChange}
                          helperText={errors.fname}
                        />
                      </div>
                      <div className="form-group col-6">
                        <TextField
                          fullWidth
                          type="text"
                          id="lname"
                          label="Last Name"
                          name="lname"
                          variant="outlined"
                          value={this.state.lname}
                          valid={errors.lname === ""}
                          invalid={errors.lname !== ""}
                          onBlur={this.handleBlur("lname")}
                          onChange={this.handleInputChange}
                          helperText={errors.lname}
                        />
                      </div>
                    </div>
                    <div className="form-group">
                      <TextField
                        fullWidth
                        type="text"
                        id="username"
                        label="Username"
                        name="username"
                        variant="outlined"
                        value={this.state.username}
                        valid={errors.username === ""}
                        invalid={errors.username !== ""}
                        onBlur={this.handleBlur("username")}
                        onChange={this.handleInputChange}
                        helperText={errors.username}
                      />
                    </div>
                    <div className="form-group">
                      <TextField
                        fullWidth
                        type="email"
                        id="email"
                        name="email"
                        label="Email"
                        variant="outlined"
                        value={this.state.email}
                        valid={errors.email === ""}
                        invalid={errors.email !== ""}
                        onBlur={this.handleBlur("email")}
                        onChange={this.handleInputChange}
                        helperText={errors.email}
                      />
                    </div>
                    <div className="form-group">
                      <FormControl
                        // className={clsx(classes.margin, classes.textField)}
                        variant="outlined"
                        fullWidth
                      >
                        <InputLabel htmlFor="outlined-adornment-password">
                          Password
                        </InputLabel>
                        <OutlinedInput
                          id="outlined-adornment-password"
                          name="pass1"
                          type={this.state.hidden1 ? "password" : "text"}
                          value={this.state.pass1}
                          onChange={this.handleInputChange}
                          onBlur={this.handleBlur("pass1")}
                          valid={errors.pass1 === ""}
                          invalid={errors.pass1 !== ""}
                          endAdornment={
                            <InputAdornment position="end">
                              <IconButton
                                style={{ outline: "none" }}
                                aria-label="toggle password visibility"
                                onClick={this.toggleShow1}
                                onMouseDown={this.toggleShow1}
                                edge="end"
                              >
                                {this.state.hidden1 ? (
                                  <Visibility />
                                ) : (
                                  <VisibilityOff />
                                )}
                              </IconButton>
                            </InputAdornment>
                          }
                          labelWidth={70}
                        />
                      </FormControl>
                    </div>
                    <div className="form-group">
                      <FormControl variant="outlined" fullWidth>
                        <InputLabel htmlFor="outlined-adornment-password">
                          Confirm Password
                        </InputLabel>
                        <OutlinedInput
                          id="outlined-adornment-password"
                          name="pass2"
                          type={this.state.hidden2 ? "password" : "text"}
                          value={this.state.pass2}
                          onChange={this.handleInputChange}
                          onBlur={this.handleBlur("pass2")}
                          valid={errors.pass2 === ""}
                          invalid={errors.pass2 !== ""}
                          endAdornment={
                            <InputAdornment position="end">
                              <IconButton
                                style={{ outline: "none" }}
                                aria-label="toggle password visibility"
                                onClick={this.toggleShow2}
                                onMouseDown={this.toggleShow2}
                                edge="end"
                              >
                                {this.state.hidden2 ? (
                                  <Visibility />
                                ) : (
                                  <VisibilityOff />
                                )}
                              </IconButton>
                            </InputAdornment>
                          }
                          labelWidth={70}
                        />
                      </FormControl>
                    </div>
                    <div className="row form-group">
                      <div className="col col-12">
                        <Button
                          className={classes.formButtons}
                          variant="contained"
                          type="submit"
                          fullWidth={true}
                          style={{
                            backgroundColor: "#09bfad",
                            color: "white",
                            outline: "none",
                            height: 50,
                          }}
                        >
                          {this.state.submitted ? (
                            <CircularProgress color="inherit" />
                          ) : (
                            "SignUp"
                          )}
                        </Button>
                      </div>
                    </div>
                  </form>
                  <div className="border-top pt-3">
                    <div className="text-muted">
                      Already have an Account?&nbsp;
                      <Link
                        to={{
                          pathname: "/login",
                          // state: {
                          //   modal: true,
                          //   redirected: this.props.redirected,
                          // },
                        }}
                      >
                        Login
                      </Link>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </div>
        <div className="refferaldiv">
          <div className="child">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
              <path
                fill="#fff"
                fill-opacity="1"
                d="M0,256L40,261.3C80,267,160,277,240,272C320,267,400,245,480,197.3C560,149,640,75,720,58.7C800,43,880,85,960,101.3C1040,117,1120,107,1200,85.3C1280,64,1360,32,1400,16L1440,0L1440,320L1400,320C1360,320,1280,320,1200,320C1120,320,1040,320,960,320C880,320,800,320,720,320C640,320,560,320,480,320C400,320,320,320,240,320C160,320,80,320,40,320L0,320Z"
              ></path>
            </svg>
          </div>
        </div>
        <br />
        <br />
      </>
    );
  }
}
SignUp.propTypes = {
  classes: PropTypes.object.isRequired,
};
export default withStyles(useStyles)(withRouter(SignUp));
