import * as actionTypes from "./actionTypes";
import { config } from "../../constants";

export const AllCoursesStart = () => ({
  type: actionTypes.ALLCOURSES_START,
});

export const AllCoursesSuccess = (allCourses) => ({
  type: actionTypes.ALLCOURSES_SUCCESS,
  allCourses: allCourses,
});

export const AllCoursesFail = (err) => ({
  type: actionTypes.ALLCOURSES_FAIL,
  error: err,
});

export const fetchAllCourses = () => (dispatch) => {
  dispatch(AllCoursesStart());

  let csrf = localStorage.getItem("csrf");

  return fetch(config.url.API_URL + "/courses/", {
    headers: {
      "Content-Type": "application/json",
      "X-CSRFToken": csrf,
    },
  })
    .then(
      (response) => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error(
            "Error " + response.status + ": " + response.statusText
          );
          error.response = response;
          throw error;
        }
      },
      (error) => {
        var errmess = new Error(error.message);
        throw errmess;
      }
    )
    .then((response) => response.json())
    .then((allCourses) => {
      dispatch(AllCoursesSuccess(allCourses));
    })
    .catch((error) => dispatch(AllCoursesFail(error.message)));
};

