export const submitQuiz = (quizId, score) => (dispatch) => {
  console.log("submitting quizz")
  const token = localStorage.getItem("token");
  let csrf = "";
  if (token) {
    csrf = localStorage.getItem("csrf");
  }
  const creds = JSON.stringify({
    score: score,
  });
  return fetch(config.url.API_URL + `/courses/submit-quiz/${quizId}`, {
    method: "GET",
    body: creds,
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      "X-CSRFToken": csrf,
      Authorization: "token " + token,
    },
  })
    .then(
      (res) => {
        if (res.ok) {
          console.log("quiz submitted");
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        throw error;
      }
    )
    .then((res) => res.json())
    .then((score) => {
      console.log(score);
      // dispatch(responseSuccess(quizResponse));
      // submitted();
    })
    .catch((err) => {
      if (err.res) {
        err.res.json().then((res) => {
          console.log(res);
          // dispatch(responseFail(res.detail));
        });
      }
    });
};

export const progressSuccess = (module_slug, content_slug) => ({
  type: actionTypes.PROGRESS_SUCCESS,
  module_slug: module_slug,
  content_slug: content_slug,
});

export const progressFail = (err) => ({
  type: actionTypes.PROGRESS_FAIL,
  error: err,
});

export const contentDone = (pk, module_slug, content_slug, success, failed) => (
  dispatch
) => {
  const token = localStorage.getItem("token");
  let csrf = "";
  if (token) {
    csrf = localStorage.getItem("csrf");
  }
  return fetch(
    config.url.API_URL +
      `/courses/mark-as-done/${pk}?module_slug=${module_slug}&content_slug=${content_slug}`,
    {
      method: "GET",
      headers: {
        Authorization: "token " + token,
        "X-CSRFToken": csrf,
      },
    }
  )
    .then(
      (res) => {
        if (res.ok) {
          return res;
        } else {
          var error = new Error("Error " + res.status + ": " + res.statusText);
          error.res = res;
          throw error;
        }
      },
      (error) => {
        throw error;
      }
    )
    .then((res) => {
      dispatch(progressSuccess(module_slug, content_slug));
      success();
    })
    .catch((err) => {
      failed();
    });
};

