import * as actionTypes from "./actionTypes";
import { config } from "../../constants";

export const profileStart = () => ({
  type: actionTypes.PROFILE_START,
});

export const profileSuccess = (profile) => ({
  type: actionTypes.PROFILE_SUCCESS,
  profile: profile,
});

export const profileFail = (err) => ({
  type: actionTypes.PROFILE_FAIL,
  error: err,
});

export const profileEdit = (profile) => ({
  type: actionTypes.EDIT_PROFILE,
  profile: profile,
});

export const fetchProfile = () => (dispatch) => {
  dispatch(profileStart());

  const token = localStorage.getItem("token");
  // let cookiearray = document.cookie.split(";");
  // let csrf = "";
  // for (var i = 0; i < cookiearray.length; i++) {
  //   let name = cookiearray[i].split("=")[0];
  //   let value = cookiearray[i].split("=")[1];
  //   if (name === " csrftoken") {
  //     csrf = value;
  //   }
  // }
  // console.log(csrf);

  // let csrf = localStorage.getItem('csrf');
  let csrf = "";
  if (token) {
    csrf = localStorage.getItem("csrf");
  }

  return fetch(config.url.API_URL + "/users/profile", {
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      Authorization: "Token " + token,
      "X-CSRFToken": csrf,
    },
  })
    .then(
      (response) => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error(
            "Error " + response.status + ": " + response.statusText
          );
          error.response = response;
          throw error;
        }
      },
      (error) => {
        var errmess = new Error(error.message);
        throw errmess;
      }
    )
    .then((response) => response.json())
    .then((profile) => {
      dispatch(profileSuccess(profile));
    })
    .catch((error) => {
      if (error.response) {
        error.response.json().then((res) => {
          dispatch(profileFail(res.detail));
          // if (res.detail === "Invalid token.") {
          //   dispatch(logout())
          // }
        });
      }
    });
};

export const editProfile = (
  first_name,
  last_name,
  date_of_birth,
  organization,
  location,
  state,
  country,
  profile,
  gender,
  std,
  contact_number,
  successEdit,
  failedEdit
) => (dispatch) => {
  const token = localStorage.getItem("token");
  // let cookiearray = document.cookie.split(";");
  // let csrf = "";
  // for (var i = 0; i < cookiearray.length; i++) {
  //   let name = cookiearray[i].split("=")[0];
  //   let value = cookiearray[i].split("=")[1];
  //   if (name === " csrftoken") {
  //     csrf = value;
  //   }
  // }

  // let csrf = localStorage.getItem('csrf');
  let csrf = "";
  if (token) {
    csrf = localStorage.getItem("csrf");
  }

  const formData = new FormData();
  if (first_name) {
    formData.append("first_name", first_name);
  }
  if (last_name) {
    formData.append("last_name", last_name);
  }
  if (date_of_birth) {
    formData.append("date_of_birth", date_of_birth);
  }
  if (location) {
    formData.append("location", location);
  }
  if (organization) {
    formData.append("organization", organization);
  }
  if (state) {
    formData.append("state", state);
  }
  if (country) {
    formData.append("country", country);
  }
  if (profile) {
    formData.append("profile_photo", profile, profile.name);
  }
  if (gender) {
    formData.append("gender", gender[0].toUpperCase());
  }
  if (contact_number) {
    formData.append("contact_number", contact_number);
  }

  return fetch(config.url.API_URL + "/users/profile", {
    method: "PATCH",
    headers: {
      Authorization: "Token " + token,
      "X-CSRFToken": csrf,
    },
    body: formData,
  })
    .then(
      (response) => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error(
            "Error " + response.status + ": " + response.statusText
          );
          error.response = response;
          throw error;
        }
      },
      (error) => {
        var errmess = new Error(error.message);
        throw errmess;
      }
    )
    .then((response) => response.json())
    .then((profile) => {
      dispatch(profileEdit(profile));
      successEdit();
    })
    .catch((error) => {
      dispatch(profileFail(error.message));
      failedEdit();
    });
};

